section.schedule .container {
  flex-direction: row;
  display: flex;
  width: min(70em, 80%);
  overflow: hidden;
  margin: 0 auto;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  background: #33cccc;
  border-radius: 50px;
}

section.schedule .dates {
  display: grid;
  min-width: 30%;
  max-width: 30%;
  padding-top: 3%;
  cursor: pointer;
}

section.schedule .category-bubble {
  position: relative;
  text-align: left;
  font-size: 1.15vw;
  height: 50%;
  padding: 15% 15% 0% 20%;
  transition: 0.1s ease-in-out;
  color: white;
  opacity: 0.6;
  outline: none; /* TODO: I don't like this */
}
section.schedule .category-bubble:hover,
section.schedule .category-bubble:focus {
  opacity: 0.9;
}

section.schedule .category-bubble.selected {
  font-size: 1.3vw;
  font-weight: bolder;
  opacity: 1;
}

section.schedule .category-bubble.selected::before {
  position: absolute;
  display: block;
  left: 10%;
  content: "â–¶";
}

section.schedule .schedule-area {
  padding: 2% 7%;
  width: 100%;
  margin-left: 0.5%;
  overflow: auto;
  position: relative;
  z-index: 40;
  border-radius: 50px;
  box-shadow: 1px 0 2px 0 rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.2);
  text-align: center;
  padding-bottom: 2vh;
}

section.schedule .schedule-area .schedule {
  display: flex;
  font-size: 19px;
  padding: 2% 0;
  padding-bottom: 1%;
  text-align: left;
  flex-direction: column;
}

section.schedule .schedule-area .schedule li.item {
  list-style: none;
  min-width: 400px;
  margin-bottom: 2.5%;
  color: white;
}

section.schedule .schedule-area .schedule li.item h6.time {
  float: left;
  width: 25%;
  padding-left: 10px;
  padding-right: 10px;
  display: inline-block;
  font: 1vw "Avenir Next", sans-serif;
  font-weight: 800;
  display: inline-block;
  vertical-align: 0.25;
}

